                <li class="layui-nav-item"><a href="">大数据</a></li>
                </ul>
               </div>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  mounted () {
        layui.use('element', function(){
		  var element = layui.element
        });
        

    },
  provide () {
    return {
      reload: this.reload
    }
  },
  data () {
    return {
      isRouterAlive: true
    }
  },
  methods: {
    reload () {
      this.isRouterAlive = false
      this.$nextTick(function () {
        this.isRouterAlive = true
      })
    }
  }
}
</script>

<style scoped>
.nav{
    margin-left: 200px;
}
.nav-side {
  
}
</style>



