<template>
  <div>
      <div id="chart_example">
        </div>
       <div id="chart_cake">
 
       </div>
  </div>
</template>

<script>
export default {
    mounted () {
         let this_ = this;
      let myChart = echarts.init(document.getElementById('chart_example'));
      let option = {
        color: ['#f44'],
        tooltip : {
          trigger: 'axis',
          axisPointer : {
            type : 'shadow'
          }
        },
        xAxis : [
          {
            type : 'category',
            data : ['1月','2月','3月','4月','5月','6月','7月','8月','9月','10月','11月','12月',],
            axisTick: {
              alignWithLabel: true
            }
          }
        ],
        yAxis : [
          {
            type : 'value'
          }
        ],
        series : [
          {
            name:'每月订单金额',
            type:'bar',
            barWidth: '60%',
            data:[33995,68666,69444,77858,85654,96236,109995,55546,88846,99225,100547,120356]
          }
        ]
      };
       let myChart1 = echarts.init(document.getElementById('chart_cake'));
    let  option1 = {
    backgroundColor: '#2c343c',

    title: {
        text: '客户旅游区域占比',
        left: 'left',
        top: 20,
        textStyle: {
            color: '#ccc'
        }
    },

    tooltip : {
        trigger: 'item',
        formatter: "{a} <br/>{b} : {c} ({d}%)"
    },

    visualMap: {
        show: true,
        min: 80,
        max: 600,
        inRange: {
            colorLightness: [0, 1]
        }
    },
    series : [
        {
            name:'旅游地区',
            type:'pie',
            radius : '75%',
            center: ['50%', '50%'],
            data:[
                {value:335, name:'亚洲'},
                {value:310, name:'北欧'},
                {value:274, name:'东南亚'},
                {value:235, name:'美洲'},
                {value:400, name:'极地地区'}
            ].sort(function (a, b) { return a.value - b.value; }),
            roseType: 'radius',
            label: {
                normal: {
                    textStyle: {
                        color: 'rgba(255, 255, 255, 0.3)'
                    }
                }
            },
            labelLine: {
                normal: {
                    lineStyle: {
                        color: 'rgba(255, 255, 255, 0.3)'
                    },
                    smooth: 0.2,
                    length: 10,
                    length2: 20
                }
            },
            itemStyle: {
                normal: {
                    color: '#c23531',
                    shadowBlur: 200,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
            },

            animationType: 'scale',
            animationEasing: 'elasticOut',
            animationDelay: function (idx) {
                return Math.random() * 200;
            }
        }
    ]
};
      myChart.setOption(option);
      myChart1.setOption(option1)
      //建议加上以下这一行代码，不加的效果图如下（当浏览器窗口缩小的时候）。超过了div的界限（红色边框）
      window.addEventListener('resize',function() {myChart.resize()})
        window.addEventListener('resize',function() {myChart1.resize()})
    }
}

</script>
<style scoped>
#chart_example{
    width: 80%;
    height: 300px;
    
    margin-left: 200px;
  }
#chart_cake{
    width: 90%;
    height: 300px;
    
    margin-left: 200px;
  }

</style>


