<template>
<div>
  <div class="coll layui-anim layui-anim-up">
      <div class="layui-collapse">
        <div class="layui-colla-item" v-for="(item,i) in lists" :key="i">
            <h2 class="layui-colla-title">{{item.title}}</h2>
            <div class="layui-colla-content layui-show detail" >
                <img :src="item.imgSrc" alt="" class="img">
                <p>{{item.desc}}</p>
                <span>价格：{{item.price}}</span>
                <span>时长：{{item.date}}</span>
                <button class="layui-btn layui-btn-danger" @click="del(item.title)">下线本产品</button>
                <button class="layui-btn layui-btn-normal" @click="open(item.title)">修改产品</button>
            </div>
            
        </div>
        
      </div>

  </div>
            <el-dialog title="商品信息" :visible.sync="dialogFormVisible">
        <el-form :model="form">
            <el-form-item label="商品价格" :label-width="formLabelWidth">
            <el-input v-model="form.price" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="商品时长" :label-width="formLabelWidth">
            <el-date-picker
            v-model="form.date"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            format="yyyy 年 MM 月 dd 日"
            value-format="yyyy-MM-dd"
            >
            </el-date-picker>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="close">确 定</el-button>
        </div>
        </el-dialog>
  </div>
</template>

<script>
export default {
    name:'List',
    inject:["reload"],
    data () {
        return {
            
            lists:'',
            dialogFormVisible: false,
            form: {
            price: '',
            date: '',
            title:''
            },
            formLabelWidth: '120px'
        }
    },
    methods: {

        del(title){
            var _this = this
            layui.use('layer', function(){
            var layer = layui.layer;
            
            layer.open({
            title: '下线产品',
            content: '确定要下载本产品吗？',
            
            yes: function(index, layero){
                console.log("aa")
                console.log(title)
                _this.$axios({
                    url:'http://localhost:8000/dellist?title='+title,
                }).then(res=>{
                    this.lists = res.data.data
                    if (res.data.code == 1) {
                        this.$message({
                        message: '下线成功',
                        type: 'success'
                        });
                    } else {
                         this.$message.error('下线失败');
                    }
                })
            }
            });     
  
            });   
  
        },
        close(){
            
            this.$axios({
            method:"POST",
            url:'http://localhost:8000/changelist',
            data:{
                    title:this.form.title,
                    price:this.form.price,
                    date:(this.form.date).join("-"),
                    
                    }
            }).then(res=>{
                console.log(res)
                if (res.data.code == 1) {
                    this.$message({
                    message: '更改成功',
                    type: 'success'
                    });
                    this.dialogFormVisible = false
                    this.reload()
                    this.$router.go(0)
                } else {
                     this.$message.error('删除失败');
                }
                
            })
            console.log(this.form)

        },
        open(title){
             this.dialogFormVisible= true
            
             this.form.title = title
              console.log(this.form)
             
        }
    },
    mounted () {
        layui.use('element', function(){
		  var element = layui.element
        });
        this.$axios({
            url:'http://localhost:8000/homelist',
        }).then(res=>{
            console.log(res)
            this.lists = res.data.data
        })

    }
}

</script>
<style scoped>
.coll{
    margin-left: 200px;
}

.img{
    display: inline-block;
    display: block;
    width: 300px;
    
}
.detail {
    position: relative;
}
.detail p{
    width: 600px;
    position: absolute;
    top: 10px;
    left: 350px;
}
.detail span{
    font-size: 18px
}
</style>


