<template>
  <div >
      <div class="card">
          <div class="layui-card lcard layui-anim-upbit layui-anim" v-for="(item,i) in list" :key="i">
            <div class="layui-card-header">用户姓名：{{item.name}}</div>
            <div class="layui-card-body">
                <p>用户邮箱：{{item.email}}</p>
                <p>用户电话：{{item.phone}}</p>
                <p>用户留言：{{item.msg}}</p>
                <p>金额单价：{{item.price}}</p>
                <p>人数：{{item.persons}}</p>
                <p>选择的商品：{{item.type}}</p>
            </div>
            <button class="layui-btn layui-btn-lg" @click="tap(item.name)">订单处理</button>
            
        </div>
      </div>
      
  </div>
</template>

<script>
export default {
    name:'Newlist',
    inject: ["reload"],
    data() {
        return {
            list:'',
            layer:''
        }
    },
    mounted(){
        
        

    },
    methods: {
        tap(name){
            var that = this
           layui.use('layer', function(){});
           layui.layer.open({
              title: '联系客户',
              content: '要为客户制定旅行计划吗？',
              yes:function(index){
                  console.log("aa")
                  
                  that.$axios({
                    url:'http://localhost:8000/delnewlist?name='+name
                }).then(res=>{
                    console.log(res)
                    if (res.data.code === 1) {
                        
                        layer.close(index)
                       // that.reload()
                        that.$router.go(0)
                         
                    } else {
                        
                    }
                    
                })
              }
              });
            //   layui.use('layer', function(){
            //     var layer = layui.layer;
                
            //     layer.msg('hello');
            //     })
       
            }           
                       
        
        },
            
            
            
            

    mounted() {
        this.$axios({
            url:'http://localhost:8000/newlist'
        }).then(res=>{
            console.log(res.data.data)
            this.list = res.data.data
        })
        
        

        
    },
}

</script>
<style scoped>
.card{
    margin-left: 200px;
    margin-top: 50px;
    
    
}
.lcard{
    background: #eee;
    width: 30%;
    display: inline-block;
    margin-left: 20px;
    box-shadow:4px 4px 10px #888
}


</style>


