<template>
  <div>

      <div class="form layui-anim layui-anim-scale">
          <form class="layui-form" action="">
      <div class="layui-form-item">
        <label class="layui-form-label">商品标题</label>
        <div class="layui-input-block">
          <input v-model="title" type="text" name="title" required  lay-verify="required" placeholder="请输入标题" autocomplete="off" class="layui-input">
        </div>
      </div>
      <div class="layui-form-item">
        <label class="layui-form-label">商品价格</label>
        <div class="layui-input-inline">
          <input v-model="price" type="text" name="price" required  lay-verify="required" placeholder="请输入价格" autocomplete="off" class="layui-input">
        </div>
        <div class="layui-form-mid layui-word-aux">每人次</div>
      </div>
      <div class="layui-form-item">
        <label class="layui-form-label">目的地</label>
        <div class="layui-input-block">
          <select name="city" lay-verify="required" v-model="area">
            <option value=""></option>
            <option value="美洲">美洲</option>
            <option value="东南亚">东南亚</option>
            <option value="北欧">北欧</option>
            <option value="亚洲">亚洲</option>
            <option value="极地地区">极地地区</option>
          </select>
        </div>
      </div>
      <div class="layui-form-item">
         <div class="layui-inline">
        <label class="layui-form-label">日期范围</label>
        <div class="layui-input-inline">
            <input v-model="date" type="text" class="layui-input" id="test6" placeholder=" - ">
        </div>
        </div>
       </div>
      
      <div class="layui-form-item t">
        <label class="layui-form-label">展示路径</label>
        <div class="layui-input-block">
          <input v-model="imgSrc" type="text" name="imgSrc" required  lay-verify="required" placeholder="请输入路径" autocomplete="off" class="layui-input">
        </div>
      </div>

      <div class="layui-form-item layui-form-text">
        <label class="layui-form-label">商品描述</label>
        <div class="layui-input-block">
          <textarea v-model="desc" name="desc" placeholder="请输入内容" class="layui-textarea"></textarea>
        </div>
      </div>
      <div class="layui-form-item">
        <div class="layui-input-block">
            <button type="button" class="layui-btn" id="test1">
            <i class="layui-icon">&#xe67c;</i>上传图片
            </button>
          <button class="layui-btn" lay-submit lay-filter="formDemo" @click="submit">立即添加</button>
          <button type="reset" class="layui-btn layui-btn-primary">重置</button>
        </div>
      </div>
    </form>
      </div>



  </div>
</template>

<script>
export default {
    name:'Addlist',
    data () {
        return {
            title:'',
            price:'',
            area:'',
            date:'',
            desc:'',
            imgSrc:''
        }
    },
    methods: {
        submit(){
            this.$axios({
                method:'post',
                url:"http://localhost:8000/addlist",
                data:{
                    title:this.title,
                    price:this.price,
                    date:this.date,
                    desc:this.desc,
                    imgSrc:this.imgSrc
                }
            }).then(res=>{
                console.log(res)
            })
        }
    },
    mounted () {
                layui.use('form', function(){
        var form = layui.form;
        
        //监听提交
        form.on('submit(formDemo)', function(data){
            layer.msg(JSON.stringify(data.field));
            
            console.log(data)
            return false;
         });
        });
        layui.use('laydate', function(){
        var laydate = layui.laydate;
        laydate.render({
            elem: '#test6'
            ,range: true
        });
        }),
        layui.use('upload', function(){
        var upload = layui.upload;
        
        //执行实例
        var uploadInst = upload.render({
            elem: '#test1' //绑定元素
            ,url: '' //上传接口
            ,done: function(res){
            //上传完毕回调
            }
            ,error: function(){
            //请求异常回调
            }
        });
        })
        
    }
}

</script>
<style scoped>
.form{
    margin-left: 200px;
    margin-top: 50px;
}
</style>


